@use "./colors" as *;
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Jost:ital,wght@0,900;1,100&family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Outfit:wght@300;400;600&display=swap');

*{
    margin: 0;
    font-family:'inter' !important;
}
body{
    position: relative;
    margin: 0;
    background-color: $background;
    &::-webkit-scrollbar { 
        display: none;  /* Safari and Chrome */
    }
    
}
#overlay{
    position:fixed;
    top: 0;
    background:$bg-Gif;
    z-index: 2 !important;
    height: 100%;
    width: 100%; 
    opacity: 3%;
    padding: 0 ;
    margin: 0;
    pointer-events: none;
}

hr{
    color: $linesGrey;
}

