@use '../../assets/Variables/colors';
@use'../../assets/Variables/elements'  as *;

.aboutUs{
    .buttomLine {
        margin-left: 25%;
    }

.aboutUs_container {

    height: fit-content;
    width: 100%;
    display: flex;
    flex-direction: column;
    color: aliceblue;
    margin-top: 100px;
    padding-bottom: 35px;
    @include componentTitle("left");
    h1{
        text-align: left !important;
        justify-content: end !important;
      }
   


    @media (min-width: 1260px) {

        flex-direction: row;

    

    }



    .imgs_container>div:nth-of-type(2n + 1) {

        width: auto;


        margin-top: 20px;


    }


    .imgs_mainContainer {
        margin-right: 60px;

        @media (max-width: 1025px) {

            margin: 8% auto 30px !important ;
    
    
    
        }



        .imgs_container {
            display: flex;
            flex-direction: row;
    
    
            .img_container {
                display: flex;
                flex-direction: column;
        
                img {
                    height: 170px;
                    width: 170px;
                    margin: 10px;
                    border-radius: 5px;
                }
        
            }
        }

    }
    

   


    .data_container {
        h1 {
            padding-top: 0px;
            margin-left: 60px !important;
            line-height: .7;
            padding-bottom: 2rem;
        }

        p {
            font-size: larger;
            width: 80%;
            margin-top: 40px;
            margin-left: 60px;

        }
    }





    @media (max-width: 768px) {

        flex-direction: column;

        .imgs_mainContainer {
            margin-right: auto !important ;
            margin-left: auto !important;
        }


        .imgs_container {
            width: fit-content;
            margin-left: auto;
            margin-right: auto;


        }

        .img_container {



            img {
                height: 120px !important;
                width: 120px !important;
                margin: 10px;
                border-radius: 5px;
            }

        }



        .data_container {

    
            p {
                margin-top: 20px ;
                margin-bottom: 60px;
                margin-left: auto ;
                margin-right: auto;
            }
        }


    }









    @media (max-width: 415px) {




        .img_container {



            img {
                height: 6rem !important;
                width: 6rem !important;
                margin: 10px;
                border-radius: 5px;
            }

        }


    }


        @media (max-width: 376px) {

    
    
            .img_container {
    
    
    
                img {
                    height: 5rem !important;
                    width: 5rem !important;
                    margin: 10px;
                    border-radius: 5px;
                }
    
            }


        }
            @media (max-width: 285px) {

    
    
                .img_container {
        
        
        
                    img {
                        height: 4rem !important;
                        width: 4rem !important;
                        margin: 10px;
                        border-radius: 5px;
                    }
        
                }
    


    }









}}