@use './colors' as *;





@mixin componentTitle($position) {

   

        .componentTitle{
            @if $position == right {
                h1{
                    font-size: 128px;
                    padding-top: 47px;
                    margin-left: 25%;
                    color:$red;
                    span{
                        color:$titlesGrey;
                    }
                }
            
                hr{
                    margin-left: 25%;   
                }
            
            
                @media (max-width: 768px) {
                    h1{
                      font-size: 350%;
                    }
                      }
        
            }@else if $position == left { 
                h1{
                    font-size: 128px;
                    padding-top: 47px;
                    text-align: end;
                    margin-right: 25%;
                    color:$red;
                    
                    span{
                        color:$titlesGrey;
                    }
                }
            
                hr{
                    margin-right: 25%;   
                }
            
            
                @media (max-width: 768px) {
                    h1{
                      font-size: 350%;
                    }
                      }
            }
            
        }
       
}