@use '../../assets/Variables/colors.scss' as *;


//FOOTER STYLE      //FOOTER STYLE      //FOOTER STYLE     //FOOTER STYLE


// STYLE OF THE CONTAINERS OF THE FOOTER

.footerContainer {
    width: 100%;
    height: auto;
    background-color: #141414;
    margin-top: 8%;





    .footerContainer>a {



        text-decoration: none;
        color: $lightWords ;


    }


    // SECTIONS CONTAINER
    .footerSectionsContainer { 
        display: flex;
        flex-direction: column;
        margin-bottom: 1.5rem;
        width: 83.333333%;
        height: 100%;
        margin-top: 3% ;
        margin-left: auto;
        margin-right: auto;
        color: $lightWords ;



        @media (min-width: 1025px) {

            flex-direction: row;
            width: 90%;

        }






        // FIRST SECTION (LOGO SECTION)

        .firstSection {
            text-align: left;
            width: 100%;
            margin-top: 1rem;




            @media (min-width: 1025px) {

                width: 20%;
                margin-right: .5rem;
            }

            @media (max-width: 768px) {

                text-align: center;
            }


            img {
                height: 178px;
                margin-left: auto;
                margin-right: auto;


            }




        }





        //SECOND SECTION (NAVIGATION SECTION)

        .secondSection {
            width: 100%;
            margin-right: .5rem;
            margin-top: 1rem;
            text-align: left;




            @media (min-width: 1025px) {

                width: 40%;

            }



            .secondSectionContainer {
                display: flex;
                flex-direction: row;

            }


        }




        // THIRD SECTION (SOCIAL SECTION)

        .thirdSection {

            width: 100%;
            margin: 1rem .0rem 0rem .0rem;
            text-align: left;




            @media (min-width: 1025px) {

                width: 20%;
                margin: 1rem .5rem 0rem .5rem;

            }



            .thirdSectionItemContainer {
                display: flex;
                flex-direction: column;
                margin-top: .75rem;

            }
        }





        // FORTH SECTION (CONTACT SECTION)

        .forthSection {

            margin-top: 1rem;
            text-align: left;



            @media (min-width: 1025px) {

                width: 40%;
                margin: 1rem .5rem 0rem .5rem;

            }






            .forthSectionItemContainer {
                display: flex;
                flex-direction: column;
                margin-top: .75rem;

            }



        }




    }

    // HEADS IN THE FOOTER
    .headItem {
        font-weight: 700;
        margin-bottom: 1.4rem;
        font-size: 30px;
        line-height: 1.75rem;
        color: #f2f2f2;
    }


    // STYLE OF EVERY ITEM IN THE FOOTER

    .footerItem {
        height: 40px;
        transition-duration: 200ms;
        margin-bottom: 2.4rem;


        span {
            padding: .5rem .5rem .5rem 0rem;
            color: #c0c0c0;
            text-align: left;
            font-size: 40px;
            line-height: 2rem;

            border-bottom: #a9a5a1 1px solid;

            @media (max-width: 768px) {

                font-size: 30px !important;

            }

            @media (max-width: 300px) {

                font-size: 1.30rem !important;

            }
        }

        span:hover {

            color: #29aae3;
            cursor: pointer;

        }


    }





    // COPY & RIGHT


    .copyRight {
        width: 100%;
        height: 4rem;
        padding-top: .25rem;
        background-color: #141414;
    }

    .copyRight>p {
        bottom: 6rem;
        text-align: right;
        color: $lightWords ;
        ;
        padding-right: 5px;



        @media (min-width: 768px) {

            margin-right: 7rem;

        }
    }



    a{
        text-decoration: none !important;
    }

}