@use '../../assets/Variables/colors';

.regumTeamContainer {
    // background-color: #141414;
    height: 1000px;
    margin-bottom: 20px;
    color: aliceblue;





    h1 {

        margin-left: 60px !important;

    }


    .secondSection {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        margin-top: 100px;



        .regumTeamData {
            width: 100%;
            padding-bottom: 20px;
            margin-left: 20px;




            .regumTeamName {
                font-size: 70px;
                font-weight: 700;
                color: #DC0447;

            }

            .regumTeamPosition {
                font-size: 62px;

            }
        }

    }




    .regumTeamPhotos {
        display: flex;

        width: 50%;


        img {

            height: 300px;
        }
    }


    .regumteamdataContainer {
        align-items: center;
        width: 50%;

        .regumTeamDataHolder {
            height: 90% !important;
        }

        .regumTeamDatasecondHolder {
            height: 60% !important;
            padding-top: 25%;
            padding-bottom: 25%;

        }

    }










    @media (max-width: 1025px) {

        height: fit-content;

        .regumteamdataContainer {
            width: 50% ;
            height: 10% !important;
            margin-left: auto;
            margin-right: auto;

        }


        .regumTeamPhotos {


            width: 80%;
            height: 700px;
            margin-right: auto;
            margin-left: auto;
        }




        .secondSection {

            .regumTeamData {



                .regumTeamName {
                    font-size: 50px !important;
                }

                .regumTeamPosition {
                    font-size: 40px !important;
                }
            }

        }


    }






    @media (max-width: 768px) {



        .secondSection {

            .regumTeamData {

                text-align: center;
                padding-top: 0%;
                padding-bottom: 0%;
                margin-left: auto;
                margin-right: auto;


                .regumTeamName {
                    font-size: 40px !important;
                }

                .regumTeamPosition {
                    font-size: 30px !important;
                }
            }

        }



        h1 {
            font-size: 3rem;
        }


        .regumTeamPhotos {

            width: 80%;
            height: 400px;
            margin-right: auto;
            margin-left: auto;
        }

        .secondSection {
            margin-top: 0px;
            flex-direction: column;
        }


        .regumteamdataContainer {
            width: 80%;
            height: 10% !important;
            margin-left: auto;
            margin-right: auto;


            .regumTeamDatasecondHolder {
                height: 60% !important;
                padding-top: 0%;
                padding-bottom: 0%;
    
            }
        }
    


    }

}