@use '../../assets/Variables/colors'as *;
@use'../../assets/Variables/elements'  as *;


.portofolio{
    z-index: 10;
    position: relative;
    height: fit-content;
    width: 100%;
   
    @include componentTitle("right");

    .buttomLine{  margin-right: 25%; 
    margin-bottom: 35px;}


.hide{
    padding: 77px 0 46px 0;
    overflow-x: hidden;
 
      .slider {   
        position: relative;
        margin-left:6%;
     
      ul {
        white-space: nowrap;
        overflow: visible;
        position: relative;
        left: 0;
        padding: 0;
        transition: left 1s;
      
      li {
        display: inline-block;
        margin-right: 37px;
        cursor: pointer;
        height: fit-content;
        .containImg{
          overflow: hidden;
          width: 719px;
          height: 359px;
          border-radius: 14px;
          img {
            object-fit: cover;
            width: 100%;
           height: 100%;
            display: block;
            opacity: 42%;         
            transition: height 1s, opacity 0.5s, margin-top 1s;

            &:hover{
              box-shadow: 0 3px 6px 0 #333;
            }
            
          }
        }
    
      
    }
   
      
      li.active img {
        margin-top: 0;
        opacity: 1;
      }

       


        @media (max-width: 840px) {
          li{
            margin-right: 20px;
          }
            .containImg{
                width:  330px !important;
                height: 165px !important;
              }
        
        }

        @media (max-height: 600px) {
          li{
            margin-right: 20px;
          }
            .containImg{
                width:  330px !important;
                height: 165px !important;
              }
        
        }


        @media (max-width: 420px) {
          li{
            margin-right: 5px;
          }
          .containImg{
              width:  210px !important;
              height: 110px !important;
            }
      
      }


    }
  }

}
.animation {
  animation-name: sss ;
  animation-duration: 2s ;
  // animation-timing-function: ease-in-out;
}
.info{
  margin-left:6%;
  width: 90%;







  .title{
font-size: 70px;
font-weight: 600;
line-height: 56.72px;
color:$red;
  }

  .data{
font-size: 25px;
font-weight: 400;
line-height: 41.88px;
color: #ffff;
margin: 16px 0 88px 0;
  }
}



@keyframes sss {
  0%{
    opacity: 0;
  }
  // 50%{
  //   opacity:0.5;
  // }
  100%{
    opacity: 1;
  }
}





a{
  color: $red !important;
  font-size: 20px;
  text-decoration: none;

  &:hover{
    text-decoration: underline;
  }
}




}

















