@use '../../assets/Variables/colors' as *;
@use'../../assets/Variables/elements'  as *;


nav{
    margin-right:60px;
    width: 100%;
    height: 252px;
    overflow: hidden !important;   
    li{
        cursor: pointer;
    }

    .upperNav{
  
        height: 103px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-image: radial-gradient(rgb(255, 255, 255), rgba(0, 0, 0, 0)) 5;

       .dataContact{
        font-size: 20px;
        width: 100%;

        ul{
            margin-left: 21% ;
      
        }
        li:nth-child(1){
            margin-right: 71px;
            &:hover{
                color:white;
              }
        }
        li:nth-child(2){
       
            color: $red;
            min-width: 170px!important;
            margin-right: 71px;
            &:hover{
                color:#f50216;
              }
        }
       }


       .rigtUpper{
        margin-right: 91px;
        font-size: 32px;
        li{
            min-width: fit-content;
        }
        li:nth-child(1){
            margin-right: 102px;
        }
      
       
    }
        ul{
            display: flex;
            margin-right: 91px;
            
            
            li{
                color:$lightWords;
                list-style: none;    
              &:hover{
                color:white;
              }
            }
        }
       
    }

    .downNav{
        display: grid;
        grid-template-columns: auto auto auto auto auto auto auto;
        margin:0px 55px 0px 55px  ;
        height:149px;
        border-left-width: 1px;
        border-left-style: solid;
        border-image: radial-gradient(rgb(255, 255, 255), rgba(0, 0, 0, 0)) 1;
        .grid{
            cursor: pointer;
           display: flex;
           align-items: center;
           justify-content: center;
             font-size: 40px;
             height: 100%;
             color:$lightWords;
       
             border-right-width: 1px;
             border-right-style: solid;
             border-image: radial-gradient(rgb(255, 255, 255), rgba(0, 0, 0, 0)) 1;
             img{
                width: 236px;
             }
             &:hover{
                color:white;
              }
        }
    }



 // Hamburger cursor
            .hamburger{
          position: absolute;
          right: 15px !important;
        display: none;
        cursor: pointer;
        // margin: auto 0;

        .bar{
        
            display: block;
            width: 25px;
            height: 3px;
            margin: 5px;
            margin-right: 20;
            background-color: $red;
            -webkit-transition: all 0.3s ease-in-out;
            transition: all 0.3s ease-in-out;
        }
            }

            .side-list{
                text-decoration: none;
                display: block !important;
                width: 100%;
                margin-left: 10% ;
                margin-top: 20%;
             li{
                margin: 25px 0;
             }
            }
        

    @media (max-width: 1433px) {
        .grid{
             
          
              font-size: 37px !important;
         
         }
    }




    @media (max-width: 1200px) {
        .dataContact{
            ul{
                margin-left: 15% !important;
            }
        }
        .grid{
             
          
            font-size: 30px !important;
       
       }
    }
    @media (max-width: 1024px) {
        .dataContact{
            font-size: 18px !important; 
            ul{
                margin-left: 5% !important;
            }

        }
        .rigtUpper{
        

            font-size: 25px !important;
        }

        .grid{
             
          
            font-size: 25px !important;
       
       }

    }

    @media (max-width: 980px) {


        .dataContact{
            ul{
                margin-left: 1% !important;
                padding: 0 !important;
                li{
                    margin: 0 !important;
                    margin-left: 20px !important;
                    font-size: 14px;
                    padding: 0 !important;
                
                }
            }
        }

        .right{
            display: block !important;
            li{
                display: none !important;
            
                }
                li:nth-child(1), li:nth-child(2){
                    display: block !important;
                }
        }
             
        .hamburger{
            display: block !important;
        
        }
    .hamburger.active{
        position: absolute;
        right: 15px !important;
    }
        .hamburger.active .bar:nth-child(2){
            opacity: 0 !important;
    
        }
        .hamburger.active .bar:nth-child(1){
           transform: translateY(8px) rotate(45deg) !important;
  
        }
        .hamburger.active .bar:nth-child(3){
            transform: translateY(-8px) rotate(-45deg) !important;
    
        }


  
        .downNav{
            display: block !important; 
            height:100px !important;
            border-left-width: 0px !important;
        }


        .grid{
             color:$lightWords;
             border-right-width: 0px !important;
             border-right-style: solid;
             border-left-width: 0px !important;
             border-left-style: solid;
   
        }






        .rigtUpper.snav{
            position: fixed !important;
             right: -1100px ;
             top:100px!important;
             height: 100% !important;
            //  background-color: black !important;
             width: 100% !important ;
             transition: 0.3s !important;
             z-index: 1111 !important;
             display: block !important;
             margin:0 !important;
             background-color: rgba(0, 0, 0, 0.733);
            
          
            
        .right{
    
            justify-content: center;
            margin: 0 auto !important;
        }
         }
         

                }
                
   
                 .active{
                    right:0px !important;
                   
                    }  
       
                    
}
@media (max-width: 980px) {
    nav{
        height: 100px;
        .logo{
            position: absolute;
        }
    }
}