//CONTACT US COMPONENT     //CONTACT US COMPONENT     //CONTACT US COMPONENT

.contactUsmainContainer {
    width: 90%;
    background-color: #141414 !important;
    padding-top: 10%;
    margin-right: auto;
    margin-left: auto;




    .contactUsContainer {

        display: flex;
        flex-direction: column;
        height: auto;
        top: 50%;





        @media (min-width: 1025px) {

            flex-direction: row;

        }


    }







    .contactUsTitle {

        display: flex;
        flex-direction: column;
        line-height: 1;
        height: 500px;
        margin-left: auto;
        margin-right: auto;
        color: #a9a5a1;
        position: relative;
        font-style: normal;
        font-weight: 400;
        font-size: 200px;
        width: 60%;
        text-align: center;
    
    
        @media (max-width: 1400px) {
    
            font-size: 150px;
    
        }
    
        @media (max-width: 1310px) {
    
            font-size: 120px;
    
        }
    
    
    
    
    
        @media (max-width: 1170px) {
    
            flex-direction: column;
            height: auto !important;
    
    
    
    
        }

        @media (max-width: 800px) {
            width: 80%;
            font-size: 80px;
    
        }
    
        @media (max-width: 300px) {
    
            font-size: 70px;
    
        }


        @media (max-width: 285px) {
    
            font-size: 50px;
    
        }
    
    }
    
    
    .contactUsTitle>p {
    
        background: linear-gradient(180deg, #DC0447 0%, rgba(220, 4, 71, 0) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
   
    
    }
    
    
    
    
    
    
    
    
    
    
    
    
    .contactUs-formContainer {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: fit-content;
        margin-left: auto;
        margin-right: auto;
        color: #ffff;
    
    
    
        @media (min-width: 1025px) {
    
            flex-direction: row;
            width: 40%;
    
        }
    }







    @media (min-width: 1170px) {
    
        .contactUsTitle>p:nth-of-type(1) {
            position: absolute;
            width: auto;
            height: 227px;
            left: 0px;
            top: -100px;
    
    
        }
    
        .contactUsTitle>p:nth-of-type(2) {
            position: absolute;
            width: auto;
            height: 228px;
            left: 210px;
            top: 100px;
            text-align: left;
    
            @media (max-width: 1250px) {
    
    
                // width: 370px;
    
    
            }
    
        }
    
        .contactUsTitle>p:nth-of-type(3) {
            position: absolute;
            width: auto;
            height: 227px;
            left: 0px;
            top: 300px;
        }
    
    }
    
    
    
    
    
    // CONTACT US FORM    // CONTACT US FORM     // CONTACT US FORM
    
    
    
    .formContainer {
        top: auto;
        padding-top: 2.5rem;
        border-radius: 0.375rem;
        height: 100%;
        width: 100%;
    
    
    
    
        @media (min-width: 768px) {
    
            height: 100% !important;
    
        }
    
        @media (min-width: 1024px) {
    
            height: 100%;
    
        }
    
    }
    
    
    .form-secondContainer {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        border-radius: 0.375rem;
        height: 100%;
    }
    
    .form-secondContainer>form {
    
        height: 100%;
    
        @media (min-width: 768px) {
    
            font-size: x-large;
    
        }
    
    }
    
    
    
    .form {
        display: flex;
        flex-direction: column;
        height: 100% !important;
    
        @media (min-width: 768px) {
    
            text-align: left;
    
        }
    
    }
    
    .h-auto {
        height: 100%;
    }
    
    
    
    // WARNING TEXT THAT THE INPUT IS REQUIRED
    .input-errMsg-container {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 1rem;
    
        @media (min-width: 768px) {
    
            flex-direction: row;
    
        }
    
    }
    
    
    .reqiredWarnMsg {
        text-align: left;
        margin-left: 0.25rem;
        font-size: 0.875rem;
        line-height: 1.25rem;
        margin-top: 4px;
    }
    
    
    // STYLE OF THE INPUT
    
    
    input:-webkit-autofill,
    select:-webkit-autofill {
    
        -webkit-text-fill-color: white;
        caret-color: #a9a5a1;
        box-shadow: #141414;
        transition: background-color 5000s ease-in-out 0s;
    }
    
    
    
    .contactUs-inputStyle {
        padding-top: .5rem;
        padding-bottom: .5rem;
        padding-left: .1rem;
        width: 100%;
        background-color: #141414;
        border: 0;
        border-bottom: #a9a5a1 1px solid;
        font-size: 35px;
        caret-color: #a9a5a1;
        color: #a9a5a1;


        @media (max-width: 768px) {
    
            font-size: 25px;
    
        }
    
    }
    
    
    
    
    @media (max-width: 768px) {
        .contactUsResponsive {
            display: flex;
            flex-direction: column !important;
        }
    
        .contactUsResponsive>div {
            width: 80% !important;
            margin-left: auto;
            margin-right: auto;
    
    
        }
    }
    
    
    
    
    .inputHolder {
        display: flex;
        flex-direction: column;
        margin-bottom: 0.5rem;
        width: 100%;
    
        @media (min-width: 768px) {
    
            width: 100%;
    
        }
    
    
    
    }
    
    .input-1-2 {
        @media (min-width: 768px) {
    
            width: 47% !important;
    
        }
    
    }
    
    
    
    
    // TEXT AREA FIELD
    
    .text-area-container {
        margin-left: auto;
        margin-right: auto;
        width: 90%;
    }
    
    
    .text-area {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        max-width: 100%;
        min-width: 100%;
        min-height: 4rem;
        margin-left: auto;
        margin-right: auto;
        border: 0;
        border-bottom: #a9a5a1 1px solid;
        font-size: 35px;
        caret-color: #a9a5a1;
        color: #a9a5a1;
    
    
        @media (max-width: 1025px) {
            min-height: 3.5rem;
        }

            
        @media (max-width: 768px) {
            min-height: 5rem;
            font-size: 25px;
        }
      
    
    }
    
    
    // SUBMIT BUTTON
    
    .submitbtn {
        background: linear-gradient(178.63deg, #DC0447 10.17%, rgba(220, 4, 71, 0) 98.84%) !important;
        color: #a9a5a1;
        border-radius: 10px;
        border: 0;
        width: 130px;
        height: 40px;
        float: right;
        font-weight: 700;
        margin-top: 1.5rem;
        margin-right: 4%;
        margin-bottom: 0.5rem;
        font-size: large;
        cursor: pointer; 
    }
    .submitbtn:focus {
        width: 125px;
        height: 38px;
    }
    
    
    
    .line {
    
        margin-top: 183px;
        width: 85%;
        margin-left: auto;
        margin-right: auto;
        background-color: #a9a5a1;
        height: 1px;
    
    
        @media (max-width: 1025px) {
    
            display: none;
        }
    
    }


}


.swal-modal {
    div {
        background-color: transparent !important;
    }




    .swal-button {
        background: linear-gradient(178.63deg, #DC0447 10.17%, #141414 98.84%) !important;
    
    }


    
    .swal-icon--success { 
        background-color: transparent !important;
        border-color:transparent !important ;
    }
    
    .swal-title {
    
    }
    
    .swal-text {
        
    }
    
}


